.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  display: none;
  width: 304px;
  padding: 36px;
  background-color: #ffffff;
  border-radius: 8px;
  transform: translate(-50%, -50%); }

.modal__form {
  width: 100%;
  height: 100%; }

.modal__title {
  margin: 0;
  margin-bottom: 24px;
  font-weight: 500;
  font-size: 28px;
  line-height: 33px;
  font-family: inherit;
  text-align: center;
  color: #333333; }

.modal__fieldset {
  margin: 0;
  padding: 0;
  border: none; }

.modal__label {
  display: block;
  cursor: pointer; }

.modal__label--login {
  margin-bottom: 23px; }

.modal__label--password {
  margin-bottom: 22px; }

.modal__label--checkbox {
  margin-bottom: 111px; }

.modal__input {
  width: 100%;
  padding: 0;
  padding-bottom: 6px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  font-family: inherit;
  color: #333333;
  border: none;
  border-bottom: 1px solid #333333;
  border-radius: 0;
  outline: none;
  appearance: none; }

.modal__input::placeholder {
  color: #828282; }

.modal__checkbox-icon {
  position: relative;
  display: block;
  width: 16px;
  height: 16px;
  margin-right: 8px;
  border: 1px solid #333333;
  border-radius: 4px; }

.modal__checkbox-icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  display: none;
  width: 10px;
  height: 8px;
  background-image: url("../img/checkbox-icon.svg");
  transform: translate(-50%, -50%); }

.modal__checkbox:checked ~ .modal__remember .modal__checkbox-icon::before {
  display: block; }

.modal__remember {
  display: flex;
  align-items: center; }

.modal__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  font-family: inherit;
  color: #333333; }

.modal__button {
  display: block;
  width: 113px;
  margin: 0 auto;
  padding: 9px 32px 10px 32px;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  font-family: inherit;
  color: #ffffff;
  background: #e5261e;
  border: none;
  border-radius: 4px;
  cursor: pointer; }

.modal__button:hover {
  background-color: #cc221b; }

@media (max-width: 1260px) {
  .modal {
    width: 284px; } }
