.search {
  width: 402px;
  padding-top: 10px;
}

.search__field {
  position: relative;
  width: 320px;
  height: 25px;
  margin-right: 28px;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  font-family: inherit;
  color: $text-default;
  border: none;
  border-bottom: 1px solid $text-default;
  border-radius: 0;
  outline: none;
  appearance: none;
}

.search__field::placeholder {
  font-size: 16px;
  line-height: 19px;
  color: #828282;
}

.search__field::-webkit-search-cancel-button {
  display: none;
}

.search__button {
  padding: 0;
  font-weight: 500;
  font-family: inherit;
  color: $text-additional;
  background: none;
  border: none;
  cursor: pointer;
}

@media (max-width: $width-desktop) {
  .search {
    width: auto;
    margin-bottom: 20px;
    padding-top: 0;
  }

  .search__field {
    width: 100%;
  }

  .search__form label {
    display: flex;
    width: 100%;
  }
}
